.profile__info {
  width: 282px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 767px) {
  .profile__info {
    width: 250px;
    align-items: flex-start;
    margin-left: 30px;
  }
}

@media (min-width: 1023px) {
  .profile__info {
    width: 550px;
  }
}
