.popup {
  font-family: 'inter', Arial, sans-serif;
  position: fixed;
  z-index: 5;
  background-color: rgba(0, 0, 0, .5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: visibility .2s, opacity .2s linear;
}

.popup:hover {
  cursor: pointer;
}
